@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(to left, #ada996, #f2f2f2, #dbdbdb, #eaeaea); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.ant-layout-header {
  padding: 0 0 0 50px;
}

.App-link {
  color: #09d3ac;
}

*{box-sizing:border-box;margin:0}html,body{margin:0;background:white;font-family:"Montserrat", helvetica, arial, sans-serif;font-size:14px;font-weight:400}.link{display:block;text-align:center;color:#777;text-decoration:none;padding:10px}.trailer_card{position:relative;display:block;width:100%;height:400px;margin:0;overflow:hidden;border-radius:10px;-webkit-transition:all 0.4s;transition:all 0.4s;box-shadow:0px 0px 120px -25px rgba(0,0,0,0.5)}.trailer_card:hover{-webkit-transform:scale(1.02);transform:scale(1.02);box-shadow:0px 0px 80px -25px rgba(0,0,0,0.5);-webkit-transition:all 0.4s;transition:all 0.4s}.movie_card{position:relative;display:block;width:800px;height:350px;margin:80px auto;overflow:hidden;border-radius:10px;-webkit-transition:all 0.4s;transition:all 0.4s;box-shadow:0px 0px 120px -25px rgba(0,0,0,0.5)}.movie_card:hover{-webkit-transform:scale(1.02);transform:scale(1.02);box-shadow:0px 0px 80px -25px rgba(0,0,0,0.5);-webkit-transition:all 0.4s;transition:all 0.4s}.movie_card .info_section{position:relative;width:100%;height:100%;background-blend-mode:multiply;z-index:2;border-radius:10px}.movie_card .info_section .movie_header{position:relative;padding:25px;height:40%}.movie_card .info_section .movie_header h1{color:black;font-weight:400}.movie_card .info_section .movie_header h4{color:#555;font-weight:400}.movie_card .info_section .movie_header .minutes{display:inline-block;margin-top:15px;color:#555;padding:5px;border-radius:5px;border:1px solid rgba(0,0,0,0.05)}.movie_card .info_section .movie_header .type{display:inline-block;color:#959595;margin-left:10px}.movie_card .info_section .movie_header .locandina{position:relative;float:left;margin-right:20px;height:120px;box-shadow:0 0 20px -10px rgba(0,0,0,0.5)}.movie_card .info_section .movie_desc{padding:50px;height:50%}.movie_card .info_section .movie_desc .text{color:black}.movie_card .blur_back{position:absolute;top:0;z-index:1;height:100%;right:0;background-size:cover;border-radius:11px}@media screen and (min-width: 768px){.movie_header{width:65%}.movie_desc{width:100%}.info_section{background:-webkit-gradient(linear, left top, right top, color-stop(50%, #e5e6e6), to(transparent));background:linear-gradient(to right, #e5e6e6 50%, transparent 100%)}.blur_back{width:80%;background-position:-100% 10% !important}}@media screen and (max-width: 768px){.movie_card{width:95%;margin:70px auto;min-height:350px;height:auto}.blur_back{width:100%;background-position:50% 50% !important}.movie_header{width:100%;margin-top:85px}.movie_desc{width:100%}.info_section{background:-webkit-gradient(linear, left bottom, left top, color-stop(50%, #e5e6e6), to(transparent));background:linear-gradient(to top, #e5e6e6 50%, transparent 100%);display:inline-grid}}

.MyCardElement {
  height: 40px;
  padding: 10px 12px;
  width: 100%;
  color: #32325d;
  background-color: white;
  border: 1px solid transparent;
  border-radius: 4px;

  box-shadow: 0 1px 3px 0 #e6ebf1;
  -webkit-transition: box-shadow 150ms ease;
  transition: box-shadow 150ms ease;
}

.MyCardElement--focus {
  box-shadow: 0 1px 3px 0 #cfd7df;
}

.MyCardElement--invalid {
  border-color: #fa755a;
}

.MyCardElement--webkit-autofill {
  background-color: #fefde5 !important;
}
.ant-carousel .slick-slide {
  text-align: center;
  height: 530px;
  line-height: 250px;
  background: #364d79;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}

.ant-carousel .slick-slide > div {
  height: 100%;
}

